header {
  background-color: #034F2C;
  color: #fff;
  padding: 10px;
}

.container-header {
  margin: auto 0;
  text-align: center;
  min-width: 600px;
  display: flex;
  justify-content: space-between;
  padding-block: 30px;
  align-items: center;
}

.container-header h1 {
  margin: 30px;
  letter-spacing: 1px;
  text-shadow: 0px 0px 5px#DFF0D8;
}

.container-header h1 {
  animation: pulse 5s ease-in-out infinite;
}

.container-header-text {
  text-align: center;
  margin: auto;
  max-width: 600px;
}

.nav ul {
  display: flex;
  list-style-type: none;
  margin-bottom: 0;
}
.nav li {
  padding-inline: 10px;
}

.nav a {
  text-decoration: none;
  color: white;
}

@media only screen and (max-width: 600px) {
  .container-header {
    flex-direction: column;
    min-width: auto;
    padding-block: 20px;
  }

  .container-header h1 {
    margin: 20px 0;
  }

  .nav {
    justify-content: center;
  }

  .nav ul {
    font-size: 0.8rem;
    padding: 0px;
  }

  .nav li {
    padding: 10px 5px;
  }
}